.archive-month {
  display: block;
  width: fit-content;
  position: relative;
  margin-top: 0.1875rem;
}

.archive-month::before {
  background: #408f95;
  content: "";
  width: 100%;
  height: 0.125rem;
  position: absolute;
  left: 0;
  bottom: 0;
  margin: auto;
  transform-origin: right top;
  transform: scale(0, 1);
  transition: transform 0.3s;
}

.archive-month:hover::before {
  transform-origin: left top;
  transform: scale(1, 1);
}

.archive-month {
  position: relative;
  margin-top: 0.1875rem;
}

.archive-month::after {
  position: absolute;
  content: "";
  display: block;
  background-image: url(../images/common/sidebar-archive-arrow.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  width: 1.5rem;
