  line-height: 1.5714;
  text-align: center;
  z-index: 5;
}

.btn:hover {
  color: #408f95;
}

.btn::before {
  position: absolute;
  border: 0.0625rem solid #408f95;
  top: 0.25rem;
  left: 0.625rem;
  right: -0.25rem;
  bottom: -0.25rem;
  content: "";
  display: inline-block;
  z-index: -1;
}

.btn span {
  position: relative;
  display: inline-block;
  width: 2.5rem;
  height: 0.0625rem;
  margin-left: 0.5rem;
  vertical-align: middle;
  line-height: 1;
  background: #fff;
  z-index: inherit;
}

.btn span:hover {
  color: #408f95;
}

.btn span::after {
  content: "";
  position: absolute;
  width: 0.625rem;
  border: 0.1125rem solid #fff;
  border-left: 0;
  border-bottom: 0;
  transform: rotate(40deg);
  transform-origin: top right;
  top: 0;
  right: 0;
  box-sizing: border-box;
  z-index: inherit;
}

.btn::after {
  position: absolute;
  background-color: #fff;
  border: 0.0625rem solid #408f95;
  color: #408f95;
  content: "";
  display: inline-block;
  height: 100%;
  left: 0;
  top: 0;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  width: 100%;
  z-index: -1;
}

.btn:hover::after {
  transform: scale(1, 1);
}

.btn:hover span {
  background-color: #408f95;
}

.btn:hover span::after {
  border: 0.08125rem solid #408f95;
}

.btn--white {
  background-color: #fff;
  color: #408f95;
}

.btn--white:hover {
  color: #fff;
}

.btn--white::before {
  border: 0.0625rem solid #fff;
}

.btn--white span {
  background: #408f95;
}

.btn--white span:hover {
  color: #fff;
}

