  max-width: 21.875rem;
}
@media screen and (min-width: 768px) {
  .modal__content--large {
    max-width: 37.5rem;
  }
}

.modal__content > img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}

/* モーダルの隠しスタイル */
.modal[style*="display: none;"] {
  display: none;
}

/* モーダルがアクティブなときのスタイル */
.modal.active {
  display: flex;
}

