.voice-card {
  padding-top: 0.9375rem;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 0.9375rem;
  box-shadow: 0.125rem 0.125rem 0.625rem rgba(0, 0, 0, 0.25);
  background-color: #fff;
  width: 100%;
  opacity: 1;
}
@media screen and (min-width: 768px) {
  .voice-card {
    padding: 1.5rem;
    min-height: 23.0625rem;
  }
}

.voice-card__info {
  display: flex;
  flex-direction: row;
  column-gap: 0.8125rem;
  align-items: end;
  justify-content: space-between;
}
@media screen and (min-width: 768px) {
  .voice-card__info {
    column-gap: 1rem;
  }
}

.voice-card__customer-data-wrapper {
  width: 100%;
}
@media screen and (min-width: 768px) {
  .voice-card__customer-data-wrapper {
    width: 58%;
  }
}

@media screen and (min-width: 768px) {
  .voice-card__customer-data {
    display: flex;
    flex-direction: row;
    align-items: end;
    justify-content: space-between;
    padding-bottom: 0.375rem;
  }
}
.voice-card__customer-age {
  font-size: 0.75rem;
  line-height: 1.5;
}

.voice-card__title-wrapper {
  margin-top: 0.25rem;
}

.voice-card__title {
  overflow-wrap: anywhere;
  font-size: 1rem;
  padding-bottom: 0.25rem;
  line-height: 1.4375;
  border-bottom: solid 0.0625rem #408f95;
  font-weight: 700;
  flex-shrink: 0;
  margin-top: auto;
}
@media screen and (min-width: 768px) {
  .voice-card__title {
    font-size: 1.5rem;
  }
}

.voice-card__img {
  position: relative;
  aspect-ratio: 180/140;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .voice-card__img {
    aspect-ratio: 150/117;
    width: 38%;
  }
}

.voice-card__img > img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: cover;
}

.voice-card__text {
  padding-top: 1rem;
  margin-top: 0;
}
@media screen and (min-width: 768px) {
  .voice-card__text {
    padding-top: 2.5rem;
  }
}

.voice-card__category {
  display: inline-block;
  padding: 0.1875rem 0.6875rem;
  font-size: 0.75rem;
  color: #408f95;
  border: solid 0.0625rem #408f95;
  line-height: 1.833333;
  width: fit-content;
}
@media screen and (min-width: 768px) {
  .voice-card__category {
    padding: 0.25rem 0.875rem;
  }
}

.voice-cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 3.0625rem;
}
@media screen and (min-width: 768px) {
  .voice-cards {
    gap: 5rem 2.5rem;
    width: 100%;
    grid-template-columns: 1fr 1fr;
  }
}

.voice__cards {
  margin-top: 2.375rem;
}

.voice__cards-inner.inner {
  max-width: 33.75rem;
}
@media screen and (min-width: 768px) {
  .voice__cards-inner.inner {
    max-width: none;
  }
}

.voice__tabs {
  max-width: 33.75rem;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .voice__tabs {
    max-width: none;
  }
}

.voice__pager-wrapper {
  margin-top: 5.25rem;
}
@media screen and (min-width: 768px) {
  .voice__pager-wrapper {
    margin-top: 4.5625rem;
  }
}

form.wpcf7-form.invalid,
form.wpcf7-form.init {
  display: flex;
  flex-direction: column;
}

input.wpcf7-form-control.wpcf7-text.wpcf7-validates-as-required {
  margin-top: 1rem;
  width: 100%;
  border: solid 0.0625rem #408f95;
  height: 2.5rem;
  padding: 0 1rem;
  background-color: #fff;
  opacity: 1;
}
@media screen and (min-width: 768px) {
  input.wpcf7-form-control.wpcf7-text.wpcf7-validates-as-required {
    margin-top: 0;
    max-width: 30rem;
    width: 106%;
  }
}

textarea.wpcf7-form-control.wpcf7-textarea.wpcf7-validates-as-required {
  margin-top: 1rem;
  width: 100%;
  border: solid 0.0625rem #408f95;
  height: 12.5rem;
  padding: 0.5rem;
}
@media screen and (min-width: 768px) {
  textarea.wpcf7-form-control.wpcf7-textarea.wpcf7-validates-as-required {
    margin-top: 0;
    max-width: 30rem;
    width: 123%;
  }
}

span.wpcf7-form-control.wpcf7-checkbox.wpcf7-validates-as-required {
  margin-top: 0.875rem;
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  span.wpcf7-form-control.wpcf7-checkbox.wpcf7-validates-as-required {
    margin-top: 0;
  }
}

.wpcf7-list-item label {
  display: flex;
  align-items: center;
  margin: 0.125rem 0 0 0;
}

.wpcf7-form-control.wpcf7-select {
  margin-top: 1rem;
  border: solid 0.0625rem #408f95;
  font-size: 0.75rem;
  position: relative;
  height: 2.5rem;
  width: 100%;
  color: transparent;
}
@media screen and (min-width: 768px) {
  .wpcf7-form-control.wpcf7-select {
    margin-top: 0;
    max-width: 30rem;
    width: 300%;
    height: 1.875rem;
  }
}

.wpcf7-form-control.wpcf7-select::after {
  position: absolute;
  top: 0.8125rem;
  right: 1rem;
  content: "";
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url(../images/common/contact-form-triangle.png);
  display: block;
  height: 0.75rem;
  width: 0.75rem;
}
@media screen and (min-width: 768px) {
  .wpcf7-form-control.wpcf7-select::after {
    top: 0.4375rem;
  }
}

.wpcf7-form-control.wpcf7-select {
  font-size: 0.75rem;
  padding: 0.625rem 2rem 0.625rem 1rem;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .wpcf7-form-control.wpcf7-select {
    padding: 0.3125rem 2rem 0.3125rem 1rem;
  }
}

span.wpcf7-form-control.wpcf7-checkbox {
  display: flex;
  flex-direction: column;
}

span.wpcf7-list-item-label {
  max-width: 15.75rem;
  width: 100%;
}
@media screen and (min-width: 768px) {
  span.wpcf7-list-item-label {
    max-width: none;
  }
}

/* Webkitブラウザ（Chrome, Safari, Opera）用 */
input.wpcf7-form-control.wpcf7-text::placeholder {
  color: #ddf0f1;
}

/* Firefox用 */
input.wpcf7-form-control.wpcf7-text::-moz-placeholder {
  color: #ddf0f1;
  opacity: 1;
}

/* Internet Explorer用 */
input.wpcf7-form-control.wpcf7-text:-ms-input-placeholder {
  color: #ddf0f1;
}

/* Edge用 */
input.wpcf7-form-control.wpcf7-text::-ms-input-placeholder {
  color: #ddf0f1;
}

/* Safari用 */
input.wpcf7-form-control.wpcf7-text::placeholder {
  color: #ddf0f1;
}

.wpcf7-spinner {
  display: none;
}

.wpcf7-not-valid-tip {
  display: none;
}

.wpcf7 form.invalid .wpcf7-response-output,
.wpcf7 form.unaccepted .wpcf7-response-output,
.wpcf7 form.payment-required .wpcf7-response-output {
  color: #c94800;
  font-size: 0.875rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: fit-content;
  margin: 0 auto;
  border-color: transparent;
  order: 1;
}
@media screen and (min-width: 768px) {
  .wpcf7 form.invalid .wpcf7-response-output,
  .wpcf7 form.unaccepted .wpcf7-response-output,
  .wpcf7 form.payment-required .wpcf7-response-output {
    flex-direction: row;
    justify-content: center;
  }
}

.wpcf7 form.invalid input[type=text],
.wpcf7 form.invalid input[type=email],
.wpcf7 form.invalid input[type=tel],
.wpcf7 form.invalid textarea {
  background-color: rgba(201, 72, 0, 0.2);
  border: solid 0.0625rem #c94800 !important;
  margin-top: 1rem;
  width: 100%;
  padding-left: 1rem;
  font-size: 1rem;
}
@media screen and (min-width: 768px) {
  .wpcf7 form.invalid input[type=text],
  .wpcf7 form.invalid input[type=email],
  .wpcf7 form.invalid input[type=tel],
  .wpcf7 form.invalid textarea {
    max-width: 30rem;
    margin-top: 0;
    width: 106%;
  }
}

.wpcf7 form.invalid input[type=text]::placeholder,
.wpcf7 form.invalid input[type=email]::placeholder,
.wpcf7 form.invalid input[type=tel]::placeholder {
  color: #fff;
}